        padding: .5em;
        line-height: 1.22;
        /* float: left; */
    }
    .item{
        margin: 0px;
        padding-left: 10px;
    }

    .profile-menu{
        position: relative !important;
        float: right !important;
        right: 0px;
    }

    .social-icons {
        /* border: 1px solid red; */
        max-width: 100px;
        margin-left: 48px;
        position: relative;
        float: right;
        right: -60px;
        /* margin- */
    }

    .icon-item{
        /* margin: 5px; */
        padding-left: 10px;
    }

    .body{
        margin-top: -2px;
        margin-left: 2px;
        margin-right: 2px;
        width: 99%;
        height: 100%;
        text-align: center;
        align-content: center;
    }

    .search-box{
        background: white;
        height: 15px;
        max-height: 15px;
    }

    .div{
        margin-top: -10px;
    }

    
}
@media (max-width:600px) {
    .sidenav {
        height: 100%;
        width: 0;
        position: fixed;
        z-index: 1;
        top: 0;
        left: 0;
        background-color: #111;
        overflow-x: hidden;
        transition: 0.5s;
        padding-top: 60px;
      }
      
      .sidenav a {
        padding: 8px 8px 8px 32px;
        text-decoration: none;
        font-size: 25px;
        color: #818181;
        display: block;
        transition: 0.3s;
      }
      
      .sidenav a:hover {
        color: #f1f1f1;
      }
    
      .color-red{
        color: red;
      }
    
        .app-title{
            text-align: left;
            width: 100%;
            min-height: 15%;
            margin-top: 8%;
        }
    
        .app-header{
            margin-bottom: 1%;
        }
    
        .fs-22{
            font-size: 22pt;
        }
    
        .shadow{
            text-shadow: 5px 4px 23px #888888;
        }
    
    }
